import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import axios from 'axios';
import '../css/Join.css';

// React-Bootstrap.
import Stack from 'react-bootstrap/Stack';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';

export default function Join() {
    const navigate = useNavigate();
    const [user, setUser] = useState(true);
    const [duplicate, setDuplicate] = useState(0);

    //    .
    useEffect(() => {
        if (user) {
            axios.get("/api/auth/status").then((response) => {
                if(response.status === 200){
                    navigate("/dashboard");
                }}).catch((error)=>{
                    setUser(false);
                    return;
                })
        }},[]);

    return (
        <Stack gap={2} className="col-md-3 mx-auto">
            <div id="join-form-style">
                <InputGroup className="mb-3">
                    <Form.Control id="joinUsername" type="text" placeholder=""/>
                    <Button id="joinDuplicate" variant="outline-secondary" onClick={()=>{
                        let username = document.querySelector("#joinUsername").value;

                        //     .
                        if (username === "") {
                            alert("  .");
                            return;
                        } else if (username.length > 255) {
                            alert("  255 .");
                            return;
                        }

                        // Back-end    .
                        axios.post("/api/auth/duplicate", {
                            username: username,
                        }).then((response)=>{
                            if (response.status === 200) {
                                alert("  .");
                                setDuplicate(1);
                            }}).catch((error)=>{
                                alert(" .");
                                    return;
                        })}}></Button>
                </InputGroup>
                <InputGroup className="mb-3">
                    <Form.Control id="joinPassword" type="password" placeholder=""/>
                </InputGroup>
                <InputGroup className="mb-3">
                    <Form.Control id="joinPasswordCheck" type="password" placeholder=" "/>
                </InputGroup>
                <InputGroup className="mb-3">
                    <Form.Control id="joinNickname" type="text" placeholder=""/>
                </InputGroup>
                <InputGroup className="mb-3">
                    <Form.Control id="joinEmail" type="email" placeholder=""/>
                </InputGroup>
                <InputGroup className="mb-3">
                    <Form.Control id="joinBirth" type="text" placeholder=""
                        onMouseOver={(e)=>{ e.currentTarget.type = "date"; }}
                        onMouseLeave={(e)=>{ e.currentTarget.type = "text"; }}/>
                </InputGroup>
                <InputGroup className="mb-3">
                    <Form.Control id="joinPhone" type="text" placeholder=""/>
                </InputGroup>
                <InputGroup className="mb-3">
                    <Form.Control id="joinAddress" type="text" placeholder=""/>
                </InputGroup>
                <div className="d-grid">
                    <Button variant="dark" onClick={()=>{
                        // Account .
                        let username = document.querySelector("#joinUsername").value;
                        let password = document.querySelector("#joinPassword").value;
                        let passwordCheck = document.querySelector("#joinPasswordCheck").value;
                        // Profile .
                        let nickname = document.querySelector("#joinNickname").value;
                        let email = document.querySelector("#joinEmail").value;
                        let birth = document.querySelector("#joinBirth").value;
                        let phone = document.querySelector("#joinPhone").value;
                        let address = document.querySelector("#joinAddress").value;

                        //    .
                        if (username === "" || password === "" || passwordCheck === "" ||
                            nickname === "" || email === "" || birth === "" || phone === "" || address === "") {
                                alert("   .");
                                return;
                        } else if (username.length > 255 || password.length > 255 || passwordCheck.length > 255) {
                            alert("  255 .");
                            return;
                        } else if (nickname.length > 50) {
                            alert("  50 .");
                            return;
                        } else if (email > 320) {
                            alert("  320 .");
                            return;
                        } else if (birth > 10) {
                            alert("  10 .");
                            return;
                        } else if (phone > 15) {
                        alert("  15 .");
                        return;
                        } else if (address > 100) {
                            alert("  255 .");
                            return;
                        }

                        //     .
                        if (duplicate === 0) {
                            alert("    .");
                            return;
                        }

                        //        .
                        if (password !== passwordCheck) {
                            alert("  .");
                            return;
                        }

                        // Back-end  .
                        axios.post("/api/auth/join", {
                            username: username,
                            password: password,
                            nickname: nickname,
                            email: email,
                            birth: birth,
                            phone: phone,
                            address: address
                        }).then((response)=>{
                            if (response.status === 200) {
                                alert(".");
                                setDuplicate(0);
                                navigate("/");
                            }}).catch((error)=>{
                                alert(" .");
                                return;
                            })}}></Button>
                </div>
            </div>
        </Stack>
    );
}
