import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import axios from 'axios';
import '../css/Login.css';

// React-Bootstrap.
import Stack from 'react-bootstrap/Stack';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';

export default function Login() {
    const navigate = useNavigate();
    const [user, setUser] = useState(true);

    //    .
    useEffect(() => {
        if (user) {
            axios.get("/api/auth/status").then((response) => {
                if(response.status === 200){
                    navigate("/dashboard");
                }}).catch((error)=>{
                    setUser(false);
                    return;
                })
    }},[]);

    return (
        <Stack gap={2} className="col-md-3 mx-auto">
            <div id="login-form-style">
                <InputGroup className="mb-3">
                    <Form.Control id="loginUsername" type="text" placeholder=""/>
                </InputGroup>
                <InputGroup className="mb-3">
                    <Form.Control id="loginPassword" type="password" placeholder=""/>
                </InputGroup>
                <div className="mb-3 d-grid">
                    <Button variant="dark" onClick={()=>{
                        let username = document.querySelector("#loginUsername").value;
                        let password = document.querySelector("#loginPassword").value;

                        //     .
                        if (username === "" || password === "") {
                            alert("   .");
                            return;
                        } else if (username.length > 255 || password.length > 255) {
                            alert("  255 .");
                            return;
                        }

                        // Back-end  .
                        axios.post("/api/auth/login", {
                            username: username,
                            password: password
                        }).then((response)=>{
                            if (response.status === 200) {
                                navigate("/dashboard");
                            }}).catch((error)=>{
                            alert("  .");
                            return;
                        })
                    }}></Button>
                </div>
                <div className="d-grid">
                    <Button variant="dark" onClick={()=>{navigate("/join");}}></Button>
                </div>
            </div>
        </Stack>
    );
}
