import { useNavigate } from 'react-router-dom';
import { useEffect, useState } from 'react';
import axios from 'axios';

import Accordion from 'react-bootstrap/Accordion';
import Modal from 'react-bootstrap/Modal';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';

export default function Logout() {
    const navigate = useNavigate();
    const [check, setCheck] = useState(0);

    //   .
    const [user, setUser] = useState(true);
    const [profile, setProfile] = useState(null);

    //  Modal .
    const [updateModal, setUpdateModal] = useState(false);
    const updateModalClose = () => setUpdateModal(false);

    //    .
    const [textPassword, setTextPassword] = useState("");
    const [textPasswordCheck, setTextPasswordCheck] = useState("");
    const [textNickname, setTextNickname] = useState("");
    const [textEmail, setTextEmail] = useState("");
    const [textBirth, setTextBirth] = useState("");
    const [textPhone, setTextPhone] = useState("");
    const [textAddress, setTextAddress] = useState("");

    //    .
    useEffect(() => {
        if (user) {
            axios.get("/api/auth/status").then((response)=>{
                if (response.status === 200) {
                    setProfile(response.data);
                    console.log(profile);
                    
                    setUser(false);
                }}).catch((error)=>{
                    alert("   .");
                })
        }},[]);

    return (
        <div>
            <div className="mb-3 d-grid">
                <Button variant="dark" size="lg" onClick={()=> {
                    axios.post("/api/auth/logout").then((response)=>{
                        if (response.status === 200) {
                            navigate("/");
                        }
                })}}></Button>
            </div>
            <div className="mb-3 d-grid">
                <Accordion>
                    <Accordion.Item eventKey="0">
                        <Accordion.Header></Accordion.Header>
                        <Accordion.Body>
                            <Tabs defaultActiveKey="update" className="mb-3">
                                <Tab eventKey="update" title="">
                                <InputGroup>
                                        <Form.Control id="updatePassword" placeholder=" " value={ textPassword } onChange={(e)=>{ setTextPassword(e.target.value) }}/>
                                        <Button variant="outline-secondary" onClick={()=>{
                                            let password = document.querySelector("#updatePassword").value;

                                            //    .
                                            if (password === "") {
                                                alert("  .");
                                                return;
                                            } else if (password.length > 255) {
                                                alert("  255 .");
                                                return;
                                            }
                                            
                                            // Back-end   .
                                            axios.post("/api/auth/verify", {
                                                password: password
                                                }).then((response)=>{
                                                if (response.status === 200) {
                                                    alert(".");
                                                    setTextPassword("");
                                                    setCheck(1);
                                                } else {
                                                    alert(" .");
                                                    setCheck(0);
                                                    return
                                                }}).catch((error)=>{
                                                    alert("   .");
                                            })}}></Button>
                                    </InputGroup>
                                    <div className="d-grid">
                                        <Button variant="outline-warning" onClick={()=>{
                                            //   .
                                            if (check === 0) {
                                                alert("   .");
                                                return;
                                            }
                                            
                                            setUpdateModal(true);
                                            }}></Button>
                                    </div>
                                    {/*   Modal */}
                                    <Modal show={updateModal} onHide={updateModalClose} animation={false}>
                                        <Modal.Header closeButton>
                                        <Modal.Title> </Modal.Title>
                                        </Modal.Header>
                                        <Modal.Body>
                                        <InputGroup className="mb-3">
                                            <Form.Control id="updatePassword" type="password" placeholder=" " value={ textPassword } onChange={(e)=>{ setTextPassword(e.target.value) }}/>
                                        </InputGroup>
                                        <InputGroup className="mb-3">
                                            <Form.Control id="updatePasswordCheck" type="password" placeholder="  " value={ textPasswordCheck } onChange={(e)=>{ setTextPasswordCheck(e.target.value) }}/>
                                        </InputGroup>
                                        <InputGroup className="mb-3">
                                            <Form.Control id="updateNickname" type="text" placeholder="" value={ textNickname } onChange={(e)=>{ setTextNickname(e.target.value) }}/>
                                        </InputGroup>
                                        <InputGroup className="mb-3">
                                            <Form.Control id="updateEmail" type="email" placeholder="" value={ textEmail } onChange={(e)=>{ setTextEmail(e.target.value) }}/>
                                        </InputGroup>
                                        <InputGroup className="mb-3">
                                            <Form.Control id="updateBirth" type="text" placeholder="" value={ textBirth } onChange={(e)=>{ setTextBirth(e.target.value) }}
                                                onMouseOver={(e)=>{ e.currentTarget.type = "date"; }}
                                                onMouseLeave={(e)=>{ e.currentTarget.type = "text"; }}/>
                                        </InputGroup>
                                        <InputGroup className="mb-3">
                                            <Form.Control id="updatePhone" type="text" placeholder="" value={ textPhone } onChange={(e)=>{ setTextPhone(e.target.value) }}/>
                                        </InputGroup>
                                        <InputGroup className="mb-3">
                                            <Form.Control id="updateAddress" type="text" placeholder="" value={ textAddress } onChange={(e)=>{ setTextAddress(e.target.value) }}/>
                                        </InputGroup>
                                        </Modal.Body>
                                        <Modal.Footer>
                                        <Button variant="secondary" onClick={updateModalClose}></Button>
                                        <Button variant="primary" onClick={()=>{
                                            // Account .
                                            let password = document.querySelector("#updatePassword").value;
                                            let passwordCheck = document.querySelector("#updatePasswordCheck").value;
                                            // Profile .
                                            let nickname = document.querySelector("#updateNickname").value;
                                            let email = document.querySelector("#updateEmail").value;
                                            let birth = document.querySelector("#updateBirth").value;
                                            let phone = document.querySelector("#updatePhone").value;
                                            let address = document.querySelector("#updateAddress").value;

                                            //    .
                                            if (password === "" || passwordCheck === "" ||
                                                nickname === "" || email === "" || birth === "" || phone === "" || address === "") {
                                                    alert("   .");
                                                    return;
                                            } else if (password.length > 255 || passwordCheck.length > 255) {
                                                alert("  255 .");
                                                return;
                                            } else if (nickname.length > 50) {
                                                alert("  50 .");
                                                return;
                                            } else if (email > 320) {
                                                alert("  320 .");
                                                return;
                                            } else if (birth > 10) {
                                                alert("  10 .");
                                                return;
                                            } else if (phone > 15) {
                                            alert("  15 .");
                                            return;
                                            } else if (address > 100) {
                                                alert("  255 .");
                                                return;
                                            }

                                            //        .
                                            if (password !== passwordCheck) {
                                                alert("  .");
                                                return;
                                            }

                                            // Back-end  .
                                            axios.post("/api/auth/update", {
                                                password: password,
                                                nickname: nickname,
                                                email: email,
                                                birth: birth,
                                                phone: phone,
                                                address: address
                                            }).then((response)=>{
                                                if (response.status === 200) {
                                                    alert(" .");
                                                }}).catch((error)=>{
                                                    alert("  .");
                                                    return;
                                                })
                                        }}></Button>
                                        </Modal.Footer>
                                    </Modal>
                                </Tab>
                                <Tab eventKey="resign" title="">
                                    <InputGroup>
                                        <Form.Control id="resignPassword" placeholder=" "/>
                                        <Button variant="outline-secondary" onClick={()=>{
                                            let password = document.querySelector("#resignPassword").value;
                                            
                                            //    .
                                            if (password === "") {
                                                alert("  .");
                                                return;
                                            } else if (password.length > 255) {
                                                alert("  255 .");
                                                return;
                                            }
                                            
                                            // Back-end   .
                                            axios.post("/api/auth/verify", {
                                                password: password
                                                }).then((response)=>{
                                                if (response.status === 200) {
                                                    alert(".");
                                                    setCheck(1);
                                                } else {
                                                    alert(" .");
                                                    setCheck(0);
                                                    return
                                                }}).catch((error)=>{
                                                    alert("   .");
                                            })}}></Button>
                                    </InputGroup>
                                    <div className="d-grid">
                                        <Button variant="outline-danger" onClick={()=>{
                                            //   .
                                            if (check === 0) {
                                                alert("   .");
                                                return;
                                            }

                                            // Back-end  .
                                            axios.delete("/api/auth/resign").then((response)=>{
                                                if (response.status === 200) {
                                                    alert(".");
                                                    navigate("/");
                                                } else {
                                                    alert(" .");
                                            }}).catch((error)=>{
                                                alert("   .");
                                        })}}></Button>
                                    </div>
                                </Tab>
                            </Tabs>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </div>
        </div>
    );
}
